@import "../../variable.scss";

.skills__me {
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .title__h2 {
    padding-bottom: 60px;
  }

  .container__skill {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;

    .line {
      width: 100%;
      height: 1px;
      background-color: $whiteCr;
      margin-bottom: 30px;
    }

    .title__h3 {
      padding-bottom: 10px;
      cursor: pointer;
    }

    .skill__active {
      margin-bottom: 18px;
      animation-name: animated;
      animation-duration: 1s;
      height: 40px;
      width: 100%;

      .container__tt {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        height: 20px;
        transition: all 1s;
      }

      .span {
        width: 570px;
        margin-bottom: 10px;
        opacity: 0;
        height: 10px;
        font-size: 0;
        transition: all 1s;
      }

      .img {

        opacity: 0;
        height: 0px;
        width: 400px;
        margin-bottom: 10px;
        border-radius: 20px;
        transition: all 1s;
      }
    }

    .skill__active.active {
      transition: all 1s ease;
      height: 100%;

      .container__tt {
        height: 230px;
        transition: all 1s;
      }

      .span.active {
        transition: all 1s;
        opacity: 1;
        width: 570px;
        height: auto;
        font-size: 16px;
      }

      .img.active {
        transition: all 2s;

        opacity: 1;
        height: 220px;
        width: 400px;
      }
    }
  }
}
