.logo {
  display: flex;
  align-items: center;

  .logo__span {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;

    .span {
      padding-bottom: 5px;
    }

    span {
      color: #FFF;
      font-family: Ubuntu;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 135%;
      letter-spacing: 3.92px;
    }
  }
}